import '../UserManagement.scss';
import React from 'react';
import { Dialog, Stack, Typography, DialogActions, Button } from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';
import { dialogButtonStyle, dialogButtonCancel } from '../../../../utils/commonStyle';
import { appapi } from '../../../../api/apiClient';
import { showmessage } from '../../../../utils/toastr'
import { useDispatch } from 'react-redux';
import { setLoading } from '../../../../redux/features/appSlice';

// //Transtion of dialog from downwards to up
// const Transition = React.forwardRef(function Transition(props, ref) {
//     return <Slide direction="up" ref={ref} {...props} />;
// });

const sx = {
    "& .MuiDialog-container": {
        alignItems: "center",
    },
    "& .MuiPaper-root": {
        width: '376px',
        minHeight: '160px',
        padding: '24px 16px 16px 16px',
        borderRadius: '4px',
    }
};

export default function DeleteUserDialog({ open, onHandleDeleteClose, user, enterpriseUser, selectedUser, deleteChecker }) {

    const dispatch = useDispatch()

    const handleDelete = async () => {
        dispatch(setLoading(true))
        let payload = []
        let params = {
            params: {
                enterpriseId: enterpriseUser.enterpriseId,
            }
        }
        if (deleteChecker) {
            selectedUser.map((item) => {
                payload.push(item.enterpriseUserId)
            })
        } else {
            payload = [user.enterpriseUserId]
        }
        const response = await appapi.delete(`user/${enterpriseUser.enterpriseId}`, params, {
            data: payload
        })
        if (response.ok) {
            dispatch(setLoading(false))
            const data = response.data;
            if (data.status === 'success') {
                showmessage(`Delete Successfully!!`)
                onHandleDeleteClose(false)
            } else {
                showmessage(data.message, 3000, 'error')
            }
        } else {
            dispatch(setLoading(false))
            showmessage(`Something Went Wrong!`, 3000, 'error')
        }
    }


    const handleClose = (event, reason) => {
        if (reason && reason === "backdropClick")
            return;
        onHandleDeleteClose(true);
    }

    return (
        <Dialog
            sx={sx}
            open={open}
            onClose={handleClose}
            // TransitionComponent={Transition}
            className='AddUserContainer'
        >
            <Stack direction='row'>
                <div className='deleteIconContainer'>
                    <DeleteIcon style={{ color: '#E62323' }} />
                </div>
                <Stack style={{ marginLeft: '16px', width: 'calc(100% - 48px)' }}>
                    <Typography style={{ color: '#E62323', marginBottom: '12px' }} className='ff-Seigo fw-semibold wordBreak'>
                        Do you want to delete {(user?.name) ? (user?.name) : (user?.emailId)}?
                    </Typography>
                    <Typography style={{ color: '#474747' }} className='ff-Seigo fw-regular fs-12 wordBreak'>
                        Once deleted {(user?.name) ? (user?.name) : (user?.emailId)} will no longer have access to the portal.
                    </Typography>
                </Stack>
            </Stack>
            <DialogActions style={{
                height: '66px',
                padding: '0',
                marginTop: '20px',
                width: '100%'
            }}>
                <Button sx={dialogButtonCancel} style={{ width: '128px' }} variant='outlined' onClick={() => onHandleDeleteClose(true)}>Cancel</Button>
                <Button sx={dialogButtonStyle} style={{ width: '136px', marginLeft: '24px', background: '#C71212' }} onClick={handleDelete} variant="contained">Delete</Button>
            </DialogActions>
        </Dialog>
    )
}
